export default {
	data() {
		return {
			params: null,
			list: null
		}
	},
	onLoad() {
		let data = uni.getStorageSync('english_name')
		this.params = data.params
		this.list = data.list
	},
	methods: {
		linkWeb(e){
			uni.navigateTo({
				url: '../webview/webview?ename=' + e
			})
		}
	}
}
